import request from '@/request/http';

// 购物车
export function queryShopCart(query) {
    return request({
        url: '/ssm/weChat/queryShopCart',
        method: 'get',
        params: query
    })
}

// 加入购物车
export function addShopCart(query) {
    return request({
        url: '/ssm/weChat/addShopCart',
        method: 'get',
        params: query
    })
}


