<template>
    <layout>
        <div class="shopping-cart">
            <cube-scroll
                ref="scroll"
                :data="shopCartList">
                <cube-swipe>
                <transition-group class="list-wrapper" name="swipe" tag="ul">
                    <li v-for="(item, index) in shopCartList" class="list-item-wrap" :key="index">
                        <cube-swipe-item
                        ref="swipeItem"
                        :btns="item.btns"
                        :index="index"
                        class="list-item"
                        @btn-click="onBtnClick">
                        <div class="img">
                            <img src="https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg" alt="">
                        </div>
                        <div class="info">
                            <div class="top">
                                <p class="name">{{item.item.itemName}}</p>
                                <p class="tip">{{item.item.goodsTexture}}</p>
                            </div>
                            <div class="price">￥{{item.item.itemPrice}} <span class="cm">/件</span></div>
                        </div>
                        <div class="buy-num">
                            <i class="add" @click="_add"></i>
                            <span class="num">{{item.item.num}}</span>
                            <i class="less" @click="_less"></i>
                        </div>
                        </cube-swipe-item>
                    </li>
                </transition-group>
                </cube-swipe>
            </cube-scroll>
        </div>
        <div class="shop-car-num">
            <div class="piece">
                共{{numTotal}}件
            </div>
            <div class="total">
                合计 ￥{{priceTotal}}
            </div>
            <div class="btn" @click="_order">下单</div>
        </div>
    </layout>
</template>
<script>
import { queryShopCart, addShopCart } from "@/api/cart.js";
const swipeData = [
        {
          item: {
            id: '3646653877',
            name: '还不是因为你长得不好看',
            desc: '伤感：歌词再狠，也抵不过现实伤人',
            imgurl: 'http://p.qpic.cn/music_cover/MhQ4bJBPt3Yt5icXyBGNhyPJnE9O51CqaN72iaDgvFmDKaia12UFhU5uQ/600?n=1'
          },
          btns: [
            // {
            //   action: 'clear',
            //   text: '不再关注',
            //   color: '#c8c7cd'
            // },
            {
              action: 'delete',
              text: '删除',
              color: '#ff3a32'
            }
          ]
        },
        {
          item: {
            id: '1789676645',
            name: '秋水浮萍任飘渺',
            desc: '『武侠配乐』快意恩仇江湖情',
            imgurl: 'http://p.qpic.cn/music_cover/8KfvDey9cibtZ5xkWxRic6vhXgdPic3wnB7reibI4pdPQBCP8u57uqcjsQ/600?n=1'
          },
          btns: [
            {
              action: 'delete',
              text: '删除',
              color: '#ff3a32'
            }
          ]
        },
        {
          item: {
            id: '3649034125',
            name: '念葳蕊',
            desc: '江海迦：热恋后哼一首歌为自己悲悯的歌',
            imgurl: 'http://p.qpic.cn/music_cover/jXFicBvicUcfIWSoCNT1OrbAoHG2fqqnrJVnGV4iaLCapWUpCKqbmAicJg/600?n=1'
          },
          btns: [
            {
              action: 'delete',
              text: '删除',
              color: '#ff3a32'
            }
          ]
        },
]
export default {
    name: 'shopping-cart',
    data() {
        return {
            title: 'Current City: BEIJING',
            shopCartList: [],
            numTotal: 0,
            priceTotal: 0,
        };
    },
    created() {
       this._queryShopCart(); 
    },
    methods: {
        _order() {
            this.$router.push({ path: '/order' });
        },
        onItemClick(item) {
            console.log('click item:', item)
        },
        onBtnClick(btn, index) {
            if (btn.action === 'delete') {
                this.$createActionSheet({
                title: '确认要删除吗',
                active: 0,
                data: [
                    {content: '删除'}
                ],
                onSelect: () => {
                    this.swipeData.splice(index, 1)
                }
                }).show()
            } else {
                this.$refs.swipeItem[index].shrink()
            }
        },
        // 列表
        _queryShopCart() {
            let params = {
                userId: 1,
            }
            queryShopCart(params).then(res => {
                res.ShopCartList.forEach(item => {
                    let obj = {
                        item: item,
                        btns: [
                            {
                                action: 'delete',
                                text: '删除',
                                color: '#ff3a32',
                            }
                        ]
                    }
                    this.shopCartList.push(obj)
                })
                this.numTotal = res.numTotal;
                this.priceTotal = res.priceTotal;
            }).catch(err => {
            })
        },
        // 加入购物车
        _add() {
            let params = {
                openId: 'oziMowdkFgTVjs0j1pRyPYj3isK0',
                itemId: 102,
            }
            addShopCart(params).then(res => {
                console.log(res)
            })
        },
        // 减掉
        _less() {

        },
    }
}
</script>
<style lang="stylus" scoped>
.shopping-cart {
    // padding-bottom: 98px;
    height: calc(100vh - 196px);
    .list-wrapper {
        background-color: #FFFFFF;
        .list-item-wrap {
        }
        .list-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 20px 30px;
            border-bottom: 0.5px solid #F2F2F2;
            .img {
                width:180px;
                height:120px;
                border-radius:8px;
                img {
                    width:180px;
                    height:120px;
                    border-radius:8px;
                }
            }
            .info {
                flex: auto;
                height:120px;
                padding-left: 21px;
                .top {
                    margin-top: 10px;
                    position: relative;
                    .name {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 28px;
                        font-family: HiraginoSansGB-W3;
                        color: #333333;
                    }
                    .tip {
                        vertical-align: top;
                        display: inline-block;
                        font-size: 18px;
                        line-height: 24px;
                        font-family: HiraginoSansGB-W3;
                        color: #ffffff;
                        padding: 0 6px;
                        background: #F89771;
                        border-radius:8px;
                    }
                }
                .price {
                    margin-top: 45px;
                    font-size: 24px;
                    font-family: HiraginoSansGB-W3;
                    font-weight: bold;
                    color: #F43030;
                    .cm {
                        font-size: 18px;
                        color: #666666;
                    }    
                }
            }
            .buy-num {
                width: 120px;
                font-size: 0px;
                .add {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    background-image: url(../../../static/image/product/add@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;
                }
                .less {
                    display: inline-block;
                    width: 40px;
                    height: 40px;
                    background-image: url(../../../static/image/product/js@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;                
                }
                .num {
                    vertical-align: top;
                    display: inline-block;
                    font-size: 24px;
                    font-family: HiraginoSansGB-W3;
                    font-weight: bold;
                    color: #333333;
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                }
            }
        }
    }
}

    .shop-car-num {
        position: fixed;
        // bottom: 122px;
        bottom 98px;
        // left: 75px;
        left: 0;
        z-index: 99;
        // width: 600px;
        width: 100%;
        height: 98px;
        line-height: 98px;
        display: flex;
        align-items: center;
        // border-radius: 10px;
        background-color: #ffffff;
        border-top: 0.5px solid #F2F2F2;
        .piece {
            padding-left: 40px;
            font-size: 24px;
            font-weight: bold;
            color: #333333;
        }
        .total {
            flex: 1;
            font-size: 24px;
            padding-left: 24px;
            font-family:HiraginoSansGB-W3;
            font-weight: bold;
            color: #333333;
        }
        .btn {
            width: 160px;
            height: 98px;
            background-color: #4A4C59;
            text-align: center;
            color: #ffffff;
            // border-top-right-radius: 10px;
            // border-bottom-right-radius: 10px;
        }
    }
</style>



